.addText {
	width: 100%;
	height: 80px;
	box-sizing: border-box;
	border-radius: 16px;
	padding: 10px;	
	font-size: 16px;
	resize: none;
}

.buttons {
	display: flex;
	justify-content: flex-end;
	margin-top: 8px;
	margin-right: 8px;


	.button {
		width: 80px;
		height: 26px;
		box-sizing: border-box;
		font-size: 16px;
		cursor: pointer;
		border-radius: 8px;
		margin-left: 5px;
		background-color: rgb(228, 37, 37);
		border: 1px solid #000;
		cursor: pointer;
	}
}

.error {
	position: absolute;
	color: red;
}

